<template>
  <div class="home">
    <nav-bar class="home-nav">购物街</nav-bar>
    <back-top></back-top>
    <pull-up-reload
      :on-infinite-load="onInfiniteLoad"
      :parent-pull-up-state="infiniteLoadData.pullUpState"
    >
      <home-swiper :prop="banner.list"></home-swiper>
      <recommend-view :prop="recommend.list"></recommend-view>
      <feature-view></feature-view>
      <tab-control :titles="tabControlTitle" @change-tab="changeCurrentTab"></tab-control>
      <keep-alive>
        <goods-list :prop="this.goods.pop.list" v-if="currentTabName === 'pop'"></goods-list>
        <goods-list :prop="this.goods.new.list" v-else-if="currentTabName === 'new'"></goods-list>
        <goods-list :prop="this.goods.sell.list" v-else></goods-list>
      </keep-alive>
    </pull-up-reload>
    <main-tab-bar></main-tab-bar>
  </div>
</template>

<script>
import MainTabBar from '@/components/content/MainTabBar'
import NavBar from '@/components/common/navbar/NavBar'
import TabControl from '@/components/content/tabControl'
import GoodsList from '@/components/content/goodsList'
import PullUpReload from '@/components/common/pullUpReload'
import BackTop from '@/components/content/backTop'

import HomeSwiper from './childrenComponents/HomeSwiper'
import RecommendView from './childrenComponents/RecommendView'
import FeatureView from './childrenComponents/FeatureView'

import { getHomeMultidata, getHomeGoods } from '@/network/home.js'

export default {
  name: 'Home',

  components: {
    NavBar,
    HomeSwiper,
    RecommendView,
    FeatureView,
    TabControl,
    GoodsList,
    PullUpReload,
    BackTop,
    MainTabBar
  },

  data() {
    return {
      // 渲染的各个列表数据
      banner: {},
      dKeyword: {},
      keywords: {},
      recommend: {},
      // 商品标题
      tabControlTitle: ['流行', '新款', '精选'],
      // 商品数据数组
      goods: {
        pop: { page: 0, list: [] },
        new: { page: 0, list: [] },
        sell: { page: 0, list: [] }
      },
      // 选中的商品标题序号
      currentTabIndex: 0,
      // 上拉加载的设置
      infiniteLoadData: {
        pullUpState: 1 // 子组件的pullUpState状态
      },
      scrollY: 0
    }
  },

  computed: {
    // 选中商品类型名称
    currentTabName() {
      let tab = 'pop'
      switch (this.currentTabIndex) {
        case 0:
          tab = 'pop'
          break
        case 1:
          tab = 'new'
          break
        case 2:
          tab = 'sell'
          break
      }
      return tab
    },
    // 选中商品类型
    currentTab() {
      let tab = this.tabs[0]
      switch (this.currentTabIndex) {
        case 0:
          tab = this.tabs[0]
          break
        case 1:
          tab = this.tabs[1]
          break
        case 2:
          tab = this.tabs[2]
          break
      }
      return tab
    }
  },

  created() {
    // 获取数据
    this.getData()

    // 获取所有商品列表
    this.getGoods('pop')
    this.getGoods('new')
    this.getGoods('sell')
  },

  activated() {
    document.documentElement.scrollTop = this.scrollY
    // 解决商品详情页返回页面位置设置无效的问题
    setTimeout(() => {
      document.documentElement.scrollTop = this.scrollY
    }, 1)
  },

  beforeRouteLeave(to, from, next) {
    this.scrollY = document.documentElement.scrollTop
    next()
  },

  methods: {
    /******
     *
     * 网络请求相关方法
     * */
    // 获取数据方法
    async getData() {
      const res = await getHomeMultidata()
      if (!res.success) {
        // console.log(res)
      } else {
        this.banner = res.data.banner
        this.dKeyword = res.data.dKeyword
        this.keywords = res.data.keywords
        this.recommend = res.data.recommend
      }
    },
    // 获取商品列表方法
    async getGoods(type) {
      this.goods[type].page++
      const res = await getHomeGoods(type, this.goods[type].page).catch((res) => {
        return res
      })
      if (res.isAxiosError) {
        // console.log('err')
        this.$toast('服务器错误，建议刷新页面重试')
      } else {
        this.goods[type].list.push(...res.data.list)
        this.goods[type].list.map((item) => {
          item.img = item.show.img
          return item
        })
      }
    },

    /******
     * 事件相关方法
     * */
    // TabControl组件发生点击回调方法
    changeCurrentTab(index) {
      this.currentTabIndex = index
    },
    // 滚动到底部后加载操作
    onInfiniteLoad(done) {
      if (this.infiniteLoadData.pullUpState === 1) {
        this.getGoods(this.currentTabName)
      }
      done()
    }
  }
}
</script>

<style lang='less' scoped>
.home-nav {
  background-color: var(--color-tint);
  color: #fff;
}
.tab-control {
  position: sticky;
  top: 44px;
  z-index: 100;
}
.content {
  height: 100%;
  overflow: hidden;
}
</style>
