<template>
  <div class="swiper" ref="swiper">
    <div
      class="swiper-itembox"
      :style="swiperStyle"
      @touchstart="touchStart"
      @touchmove.prevent="touchMove"
      @touchend="touchEnd"
    >
      <swiper-item v-for="item in slideList" :key="item.key" class="swiper-item">
        <a :href="item.link" class="swiper-link">
          <img :src="item.image" alt="" class="swiper-img" />
        </a>
      </swiper-item>
    </div>
    <div class="circleBox">
      <div
        class="circle"
        v-for="(item, index) in prop"
        :key="index"
        :class="{ currentcircle: index === currentIndex - 1 }"
      ></div>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
import SwiperItem from './SwiperItem'

export default {
  components: {
    SwiperItem
  },
  props: {
    // 传入的数据列表
    prop: {
      type: Array,
      require: true
    },
    interval: {
      type: Number,
      default: 3000
    },
    animDuration: {
      type: Number,
      default: 300
    },
    moveRatio: {
      type: Number,
      default: 0.25
    },
    showIndicator: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      slideList: [],
      slideCount: 0, // 元素个数
      totalWidth: 0, // swiper宽度
      swiperStyle: {
        transition: '',
        transform: 'translate(-100vw, 0)'
      }, // swiper样式
      currentIndex: 1, // 当前显示序号
      scrolling: false, // 是否自动滚动中
      touchStartX: 0
    }
  },
  mounted() {
    this.initList()
    this.startTimer()
    this.addResizeListener()
  },
  methods: {
    /**
     * 定时器操作
     */
    startTimer() {
      this.playTimer = window.setInterval(() => {
        this.currentIndex++
        this.scrollContent()
      }, this.interval)
    },
    stopTimer() {
      window.clearInterval(this.playTimer)
    },
    // 滚动控制
    scrollContent() {
      // 设置正在滚动
      this.scrolling = true
      // 添加延时
      this.swiperStyle.transition = 'transform ' + this.animDuration + 'ms'
      this.setTransform()
      // 判断滚动到的位置
      this.checkPosition()
      // 滚动完成
      this.scrolling = false
    },
    // 判断当前滚动位置，当要超出边际时自动调整当前页数
    checkPosition() {
      setTimeout(() => {
        this.swiperStyle.transition = ''
        if (this.currentIndex === 0) {
          this.currentIndex = this.slideCount
          this.setTransform()
        } else if (this.currentIndex === this.slideCount + 1) {
          this.currentIndex = 1
          this.setTransform()
        }
      }, this.animDuration)
    },
    // 初始化数据列表
    initList() {
      /* ***
       * 获取swiper宽度
       * created时dom未渲染无法获取ref-swiper
       * 导致初始化需要在mouted中进行
       ****  */
      this.totalWidth = this.$refs.swiper.offsetWidth
      // 将传入的数组克隆并记录数据条数
      this.slideList = _.cloneDeep(this.prop)
      this.slideCount = this.slideList.length

      // 如果数组数目大于1则在前后添加用于平衡滚动视效的slide
      if (this.slideCount > 1) {
        const firstSlide = _.cloneDeep(this.slideList[0])
        const lastSlide = _.cloneDeep(this.slideList[this.slideList.length - 1])

        this.slideList.push(firstSlide)
        this.slideList.unshift(lastSlide)

        this.slideList = this.slideList.map((val, index) => {
          val.key = index
          return val
        })
      }
    },
    // 设置位移
    setTransform(val = 0) {
      const displace = this.currentIndex * -this.totalWidth + val
      this.swiperStyle.transform = `translate(${displace + 'px'},0)`
    },
    touchStart(e) {
      if (this.scrolling) return
      this.stopTimer()
      this.touchStartX = e.touches[0].pageX
    },
    touchMove(e) {
      // 获取实时拖动位置
      const movingX = e.touches[0].pageX
      // 获取拖动距离
      this.moveDisplacement = movingX - this.touchStartX
      // 改变负责拖动处理的盒子位置
      this.setTransform(this.moveDisplacement)
    },
    touchEnd(e) {
      // 获取拖动距离绝对值并与页面宽度比较
      const currentMove = Math.abs(this.moveDisplacement)

      if (this.moveDisplacement === 0) {
        return
      } else if (this.moveDisplacement > 0 && currentMove > this.totalWidth * this.moveRatio) {
        // 如果右滑且距离超过一定比例 则前翻一页
        this.currentIndex--
      } else if (this.moveDisplacement < 0 && currentMove > this.totalWidth * this.moveRatio) {
        // 如果右滑且距离超过一定比例 则后翻一页
        this.currentIndex++
      }
      // 刷新盒子位置
      this.scrollContent()
      // 重启定时器
      this.startTimer()
    },
    addResizeListener() {
      window.addEventListener(
        'resize',
        // 添加防抖减少开销
        _.debounce(() => {
          return (() => {
            // 修改窗口宽度
            this.totalWidth = this.$refs.swiper.offsetWidth
            // 调整位移大小
            this.setTransform()
          })()
        }, 12)
      )
    }
  }
}
</script>

<style lang='less' scoped>
.displacement {
  width: 1000;
}

.swiper {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.swiper-itembox {
  display: flex;
  .swiper-link {
    height: 100%;
  }
}

.circleBox {
  display: flex;
  position: absolute;
  bottom: 5px;
  left: 50%;
  transform: translate(-50%);
  .circle {
    background-color: rgba(255, 255, 255, 0.8);
    width: 8px;
    height: 8px;
    border-radius: 50%;
    margin: 0 2px;
  }
  .currentcircle {
    background-color: rgb(212, 62, 46);
  }
}
</style>
