<template>
  <div class="tab-control">
    <ul class="title-box">
      <li
        v-for="(item, index) in titles"
        :key="index"
        class="title-item"
        :class="{ currentItem: index === currentItem }"
        @click="changeCurrent(index)"
      >
        <span> {{ item }}</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    titles: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      currentItem: 0
    }
  },
  methods: {
    changeCurrent(index) {
      this.currentItem = index
      this.$emit('change-tab', this.currentItem)
    }
  }
}
</script>

<style lang='less' scoped>
.tab-control {
  background-color: #fff;
}
.title-box {
  display: flex;
  width: 100%;
  .title-item {
    flex: 1;
    text-align: center;
    height: 40px;
    line-height: 40px;
    font-size: 15px;
  }
  .currentItem span {
    color: #ff5777;
    padding: 5px;
    border-bottom: 3px solid #ff5777;
  }
}
</style>
